/* Estilos CSS en un archivo .css separado o en línea en el componente */

.card {
    display: flex;
    width: 500px;
    margin: 10px 70px;
    border: 6px solid rgb(0, 255, 200);
    border-radius: 4px;
    box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, .5);
    padding: auto;
  }
  
  
  