/* Estilos para la barra de navegación */
nav {
    background-color: #00000075; /* Color de fondo de la barra de navegación */
  }
  
  ul {
    list-style-type: none; /* Elimina los puntos de la lista */
    margin: 6px;
    padding: 6px;
    display: flex; /* Hace que los elementos de la lista se muestren en línea horizontalmente */
  }
  
  li {
    margin-right: 20px; /* Espacio entre elementos de la lista */
  }
  
  a {
    text-decoration: none; /* Elimina el subrayado de los enlaces */
    color: white; /* Color del texto de los enlaces */
    font-weight: bold; /* Negrita para el texto de los enlaces */
  }
  
  /* Cambiar el color del enlace cuando se pasa el mouse sobre él */
  a:hover {
    color: #00ffc3;
  }
  